import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./reorder-level.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ReorderLevelComponent  {
  @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
  @ViewChild(ToastComponent) toast: ToastComponent;
 
  reorderForm: FormGroup;
   public branches = [];
   public locations= [];
   public categorys= [];
   reorder= new ReorderData();
   public categoriesparents = [];
   public status ="";
   msg = [];
   configDetails = new ConfigDetails();
   configInventoryLocationName='';
	//config = new configDetails(); 
   stockLocationId = 0;
   reorderId = 0;
   bootstrapFromValues = [];

     constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
    	this.route.queryParams.filter(params => params.stockLocationId).subscribe(params=>{
			if(params.stockLocationId != undefined){
				this.stockLocationId=params.stockLocationId; 
			}
		}); 
	console.log("stockLocationId=="+this.stockLocationId);
	if(this.stockLocationId == 0){
		this.status = "Add";
		this.populateDropDownData();
	}else{
		this.populateData();
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.reorderForm = new FormGroup({			
			'branchId' : new FormControl('', [ Validators.required]),
			'locationId' : new FormControl('', [ Validators.required]),
			'categoryId' : new FormControl('',)
		});
		
	}
	  
	   	get branchId(){ return this.reorderForm.get('branchId')};
	 	get locationId(){ return this.reorderForm.get('locationId')};
        get categoryId(){ return this.reorderForm.get('categoryId')};
	 
	 
	 
	   populateDropDownData(){
		
			  
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
			 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch', {},
									{ headers, withCredentials: true }).subscribe(data =>{				   
				  														this.branches = data['populateBranch'];
			  															});
		     this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/configdetails',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.configDetails = data['populateconfigdetails'][0];
				  														console.log(this.configDetails);
			  															});
//		    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/location', {},
//									{ headers, withCredentials: true }).subscribe(data =>{				   
//				  														this.locations = data['populateLocation'];
//			  															});
//		    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/category', {},
//									{ headers, withCredentials: true }).subscribe(data =>{				   
//				  														this.categorys = data['populateCategory'];
//			  															});
			 	  
	 }

        branchOnChange(event){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/location',
       								 JSON.stringify({ branchId :event.value}),
		    						{ headers, withCredentials: true }).subscribe(data =>{
																	this.locations = data['populateLocation'];
																
																	}); 
	}

        branchOnchangeUpdate(element){
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/location',{branchId: element},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.locations = data['populateLocation'];
			  															});
	 }

       locationOnChange(event){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/category',
       								 JSON.stringify({ locationId :event.value}),
		    						{ headers, withCredentials: true }).subscribe(data =>{
																	this.categorys = data['populateCategory'];
																
																	}); 
	}
        locationOnchangeUpdate(element){
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/category',{locationId: element},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.categorys = data['populateCategory'];
			  															});
	 }

      populateData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
	    	   this.http.post<ReorderData>(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/reorder-level-data',
						 			JSON.stringify({ stockLocationId :this.stockLocationId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.reorder = data['populateData'][0];
																		console.log(this.reorder);
			  															},
				            											 (err) => {
							    						    				console.log("ERROR: "+err);
								             							 },
											            				 () => {
			  														this.populateDropDownData();
			                                                        if(this.reorder.branchId!='0'){
				                                                    this.branchOnchangeUpdate(this.reorder.branchId);
				   			                                        }
			             			                               if(this.reorder.locationId!='0'){
				                                                   this.locationOnchangeUpdate(this.reorder.locationId);
				             			                            }
								                              });
		}

 
	   ngAfterViewInit(){ 
	   
	   }	
	
 
	   toggleAnimation(){
			this._router.navigate(['inventory/reorder-level/reorder-update']);      //path should be changed
		 }

 
	   onSubmit() {
                      console.log(this.reorder);
		              if(!this.reorderForm.invalid){
		              const headers = new HttpHeaders({'Content-Type':'application/json'});
	                  if(this.stockLocationId == 0){
		              this.reorder['requestType'] = 'add';
	                  }else{
			          this.reorder['requestType'] = 'update';
			          this.reorder['stockLocationId'] = this.stockLocationId+'';
		              }
			             this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/reorder-level',
				         JSON.stringify(this.reorder),
					     { headers, withCredentials: true }).subscribe(data => {
						 if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
														this.toggleAnimation();
			 											this.reorderForm.reset();
														}
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }
	  }
 
  onDelete(){
//		 this.categories['requestType'] = 'delete';
//		this.categories['catId'] = this.catId+'';
//		 const headers = new HttpHeaders({'Content-Type':'application/json'});
//		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/categories',
//									JSON.stringify(this.categories),
//						{ headers, withCredentials: true }).subscribe(data => {			 											
//			 											if(data['successmsg'].includes('Successfully')){
//															this.msg[0]='success';
//															this.msg[1]=data['successmsg'];
//														}else{
//															this.msg[0]='Error';
//															this.msg[1]=data['successmsg'];
//														}
//			 											this.toast.showtoast(this.msg);
//			 											this.toggleAnimation();
//			 											this.categoriesForm.reset();
////			 											this.list.getData();
//														},
//														err =>{
//			 												console.log(err['error']['desc']);
//			 												this.msg[0]='Error';
//			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
//		 												});
//	 }
	  	
	
}

}
	
export class ReorderData {

    branchId: string;
    locationId:string;
    categoryId:string;
}

export class ConfigDetails{
	configInventoryLocationName:string;
}





